body {
  margin: 0;
  padding: 0;
  font-family: sans-serif;
}

#grid {
  display: grid;
  grid-template-columns: 50px 50px 50px 50px 50px 50px 50px 50px 50px 50px;
  text-align: center;
  padding-left: 25%
}

#point {
  background-color: green;
}

#snakeBody {
  background-color: rgb(76, 76, 230);
}

#snakeHead {
  background-color: rgb(28, 28, 172);
}

span {
  height: 100%;
  width: 100%;
  border-radius: 0px;
  background-color: black;
}


